        div.cardPromotion{
            position: relative;
            margin-bottom: 6rem;
            width: 25rem;
            margin-left: 6rem;
            div.cardImageDiv{
                width: 23rem;
                justify-content: center;
                align-items: center;
                img{
                    max-width: 100%;
                    max-height: 100%;
                    position: relative;
                }
            }
            div.shopIconDiv{
                cursor: pointer;
                width: 120px;
                height: 120px;
                position: absolute;
                background-color: var(--color-blue);
                border-radius: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                top: 330px;
                left: 130px;
                img{
                    max-width: 100%;
                    max-height: 100%;
                    position: relative;
                }
            }
            div.promotion{
                width: 80px;
                height: 50px;
                position: absolute;
                background-color: var(--color-primary);
                display: flex;
                justify-content: center;
                align-items: center;
                top: 1rem;
                right: 3.2rem;
                p{
                    position: relative;
                    font-size: 18pt;
                    font-weight: 700;
                    color: wheat;
                }
            }
            div.aboutCard{
                margin-top: 40px;
                width: 25rem;
                padding-bottom: 22px;
                border-bottom: 2px solid var(--color-grayLight);
                p{
                    font-size: 16pt;
                    margin: 2px 0;
                }
                p.brakeRoll{
                    margin-top: 18px;
                }
                strong{
                    font-weight: 700;
                }
            }
            p.descrition{
                margin: 8px 0;
                font-size: 14pt;
            }
        }