:root{
    --color-brown: #24170a;
    --color-danger: #ff018c;
    --color-primary: #b052e8;
    --color-primary2: #71239e;
    --color-blue: #5ba3b5;
    --color-primary3: #451361;
    --color-secoundary:  rgb(254, 241, 255);
    --color-grayLight:  rgb(200, 200, 200);
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background-color: rgb(254, 241, 255);
    color: #1d1d1e;
    //padding: 0 36px;
}

body, input, button, textarea{
    font-family: 'Roboto', sans-serif;
}

.swiper {
    padding-left: 3.5vw;
}

a{
    text-decoration: none;
}



@media (max-width: 1550px) {
    .swiper {
        padding-left: 0.5vw;
    }
  }

@media (max-width: 980px) {
    .swiper {
        padding-left: 22.5vw;
    }
}

@media (max-width: 768px) {
    .swiper {
        padding-left: 9.5vw;
    }
  }

  div.gridBrands{
    .swiper {
        padding-left: 9.5vw;
    }
}

.addProductForm{
    width: 30rem;
    background-color: wheat;
    padding: 3rem;
    margin: 3rem;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 12px;
    
    input{
        border-radius: 7px;
    }
    button{
        border-radius: 7px;
    }
    h2{
        margin: 1rem;
    }
}