section.section1{
    div.banner{
        position: relative;
        img.banner{
            width: 100vw;
        }
        div{
            position: absolute;
            top: 12vw;
            left: 17.4vw;
            text-align: center;
            p.title{
                font-size: 4.4vw;
                font-weight: 700;
                color: var(--color-primary2);
            }
            p.subtitle{
                font-size: 3.4vw;
            }
        }
    }
    div.circles{
        display: flex;
        justify-content: center;
        margin: 20px 0;
        align-items: center;
        div.circle{
            width: 16px;
            height: 16px;
            margin: 0 12px;
            border-radius: 100%;
            background: var(--color-primary);
        }
        div.active{
            width: 24px;
            height: 24px;
            background: var(--color-primary3);
        }
    }
}

section.section2{
    div.title{
        display: flex;
        justify-content: space-around;
        align-items: center;
        margin: 3rem 0;
        div.line{
            width: 500px;
            height: 1.4px;
            background-color: var(--color-primary);
        }
        p{
            font-weight: 700;
            color: var(--color-primary);
            font-size: 16pt;
            text-align: center;
        }
    }
}

section.section3{
    margin-top: 3rem;
    div.banner{
        position: relative;
        img.banner{
            width: 100vw;
        }
        div{
            position: absolute;
            top: 3.8vw;
            left: 6vw;
        }
        p{
            font-size: 1.4vw;
            color: white;
        }
        strong{
            font-size: 2.2vw;
            color: wheat;
        }
        input.inputText{
            width: 30vw;
            height: 3vw;
            padding-left: 2vw;
            border-radius: 30px;
            border: none;
            font-size: 1.3vw;
            outline: none;
            margin-top: 1rem;
        }
        input.inputSubmit{
            background-color: var(--color-primary);
            width: 6vw;
            height: 3vw;
            border-radius: 30px;
            border: none;
            outline: none;
            font-size: 1.3vw;
            margin-left: 1.4vw;
            color: white;
        }
    }
}

