main.success{
    background-image: url("../assets/background1.png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 75vh;
    display: flex;
    justify-content: center;

    div.content{
    section.section1{
        padding-top: 4rem;
        text-align: center;
        margin-bottom: 3rem;
        p.subtitle{
            //color: var(--color-gray);
            color: white;
            font-size: 20pt;
        }
        p.title{
            font-weight: 700;
            font-size: 40pt;
            color: var(--color-primary2);
        }
    }

    section.message{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        background-color: white;
        border-radius: 30px;
        width: 25rem;
        height: 25rem;
        padding: 1rem;
        p{
            text-align: center;
            font-size: 3rem;
            color: var(--color-primary);
        }
        
    }

        
}
}