main.cart{
    //background-image: url("../assets/background1.png");
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;

    div.content{
    section.section1{
        padding-top: 4rem;
        text-align: center;
        margin-bottom: 3rem;
        p.subtitle{
            //color: var(--color-gray);
            color: var(--color-gray);
            font-size: 20pt;
        }
        p.title{
            font-weight: 700;
            font-size: 40pt;
            color: var(--color-primary2);
        }
    }

    section.table{
        background-color: rgb(238, 238, 238);
        padding: 4rem;
        border-radius: 30px;
        font-size: 18pt;
        margin-bottom: 4rem;
        p{
            text-align: center;
            font-weight: 400;
            font-size: 40pt;
            color: var(--color-primary);
            margin-bottom: 2rem;
        }
            th{
                padding: 1.4rem 6rem;
                text-align: left;
                border-bottom: 3px solid var(--color-primary);
            }
            td{
                font-weight: 300;
            }
                button{
                    background-color: var(--color-danger);
                    color: white;
                    padding: 1rem;
                    font-size: 14pt;
                    font-weight: 700;
                    outline: none;
                    border: none;
                    margin: 1rem;
                    border-radius: 20px;
                
            }
        
    }

        
}
}