main.signIn{
    background-color: var(--color-brown);
    background-image: url("../assets/background1.png");
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;

    div.gridSign{
        display: grid;
        margin: 1rem 0rem;
        cursor: pointer;
        div.iconSign{
            border: 1px solid #dddddd;
            background-color: var(--color-primary);
            color: white;
            border-radius: 24px;
            padding: 1rem;
            align-items: center;
            display: grid;
            img{
                width: 60px;
                background-color: white;
                border-radius: 30px;
                margin: 0.8vw 5.2vw;
            }
        }
    }
    section{
        justify-content: center;
        div.logo{
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                margin: 2rem;
            }
        }
        div.form{
            width: 26rem;
            border-radius: 30px;
            justify-content: center;
            display: flex;
            align-items: center;
            padding: 2.5rem;
            background-color: white;
            div{
                div.signIn, div.signUp{
                    justify-content: center;
                    margin: 1rem 0;
                }
                div.signIn2{
                }
            }
        }
    }

}

input{
    width: 20rem;
    height: 3rem;
    margin: 0.4rem 0;
    outline: none;
    border: none;
    background-color: white;
    font-size: 13pt;
    color: var(--color-primary);
    border-bottom: 2px solid var(--color-primary);
}

input::placeholder {
    
}

strong{
    font-size: 14pt;
    color: var(--color-primary);
    font-weight: 700;
}

button.button1, button.button2{
    outline: none;
    border: none;
    width: 20rem;
    height: 3rem;
    background-color: var(--color-primary);
    border-radius: 5px;
    color: white;
    font-size: 13pt;
}

button.button2{
    background-color: white;
    border: 2px solid var(--color-primary);
    color: var(--color-primary);
}
