header{
    background-color: var(--color-primary);
    display: flex;
    justify-content: space-between;
    padding: 30px 60px;
    align-items: center;
    img.logo{
        width: 40px;
    }
    div.search{
        display: flex;
        align-items: center;
        background-color: var(--color-secoundary);
        padding: 10px;
        border-radius: 30px;
        img.searchIcon{
            width: 40px;
            margin: 0 12px;
        }
        input.searchInput{
            margin: 0 12px;
            border: none;
            outline: none;
            font-size: 12pt;
            background-color: var(--color-secoundary);
        }
        div.divide1{
    height: 30px;
    width: 1px;
    background-color: var(--color-grayLight);
        }
        img.closeIcon{
            margin: 0 12px;
            width: 10px;
        }
    }
    ul{
        display: flex;
        list-style: none;
        text-decoration: none;
        align-items: center;
        text-align: center;
        
        li, a{
            margin: 0 26px;
            font-size: 14pt;
            color: white;
            cursor: pointer;
            transition: font-weight 0.5s ease, color 0.5s ease;
        }
        li, a:hover {
            font-weight: 700;
            color: var(--color-primary2);
          }
        img.cartIcon{
            width: 50px;
        }
        div.divide2{
            height: 30px;
            width: 1px;
            background-color: white;
        }
    }

    .menu-icon, .menuResponsive, .menuResponsiveHidden{
        display: none;
    }
}

    div.cart{
        position: absolute;
        right: 0;
        top: 0;
        margin-top: 10rem;
        margin-right: 3rem;
        z-index: 101;
        background-color: white;
        width: 50rem;
        height: 30rem;
        border-radius: 12px;
        box-shadow: 0px 0px 20px 2px rgba($color: black, $alpha: 0.05);
        padding: 3rem 0rem;

        div.title{
            font-size: 18pt;
            padding-bottom: 1rem;
            border-bottom: 1px solid wheat;
            padding-left: 3rem;
            padding-right: 3rem;

        }
        div.gridItemsCart{
            padding: 1rem 0rem;
            background-color: rgb(250, 250, 250);
            height: 75%;
            overflow: auto;

            padding-left: 3rem;
            padding-right: 3rem;
            .itemCart{
                align-items: center;
                justify-content: space-between;
                display: flex;
                margin: 1rem 0;
                .productData{
                    display: flex;
                    align-items: center;
                    justify-content: space-around;
                    img{
                        width: 50px;
                    }
                    p{
                        margin-left: 1rem;
                        font-weight: 700;
                    }
                }
                .trash{
                    background-color: rgb(255, 108, 108);
                    width: 3rem;
                    height: 3rem;
                    border-radius: 6px;
                    padding: 0.9vw;
                }
            }
        }
        div.total{
            display: flex;
            justify-content: space-between;
            font-size: 14pt;
            border-top: 1px solid wheat;
            padding-top: 1rem;
            padding-left: 3rem;
            padding-right: 3rem;
            div.close{
                background-color: red;
                color: white;
                padding: 1rem;
                border-radius: 12px;
                font-weight: bold;
            }
            div.quantity{
                background-color: wheat;
                padding: 1rem;
                border-radius: 12px;
                font-weight: bold;
            }
            div.totalPrice{
                background-color: var(--color-primary);
                padding: 1rem;
                border-radius: 12px;
                font-weight: bold;
                color: wheat;
            }
            div.finish{
                background-color: rgb(9, 128, 9);
                padding: 1vw;
                border-radius: 0.6vw;
                //font-weight: bold;
                color: wheat;
            }
        }
    }
   
    div.cartResponsiveHidden{
        display: none;
    }

    div.openCart{
        display: flex;
    }














