@media screen and (max-width: 1050px) {
    header{
        position: relative;
        ul{
            display: none;
        }
        div.search{
            display: none;
        }
        .visibility{
            width: 3rem;
            height: 3rem;
            background-color: wheat;
            display: block;
        }
        .menu-icon{
            display: block;
            color: wheat;
            cursor: pointer;
            font-size: 3rem;
        }
    }

    .menuResponsive{
        transition: transform 0.3s ease-in-out;
        transform: translateX(-100%);
    }

    div.open{
        display: block;
        background-color: var(--color-primary);
        opacity: 90%;
        position: absolute;
        width: 100vw;
        height: 100vh;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 100;
        padding:7vh 10vh 10vh 10vh;
        justify-content: center;

        transform: translateX(0);
    

        .close-icon{
            color: wheat;
            cursor: pointer;
            right: 0;
            font-size: 3rem;
        }
        ul{
            display: grid;
            height: 10rem;
            align-items: center;
            justify-content: center;
            li{
                margin: 2rem 0 2rem 0;
            }
            div.divide2{
                display: none;
            }
        }
        div.search{
            display: flex;
            width: 100%;
            justify-content: center;
            margin-bottom: 3rem;
        }
    }



    div.cart{
        position: absolute;
        right: 0;
        top: 0;
        margin-top: 10rem;
        margin-right: 3vw;
        z-index: 101;
        background-color: white;
        width: 92vw;
        height: 40vw;
        border-radius: 12px;
        box-shadow: 0px 0px 20px 2px rgba($color: black, $alpha: 0.05);
        padding: 3vw 0vw;
        div.title{
            font-size: 2.4vw;
            padding-bottom: 1vw;
            border-bottom: 1px solid wheat;
