            border-top: 1px solid wheat;
            padding-top: 1vw;
            padding-left: 3vw;
            padding-right: 3vw;
            div.close{
                background-color: red;
                color: white;
                padding: 1vw;
                border-radius: 0.6vw;
                //font-weight: bold;
            }
            div.quantity{
                background-color: wheat;
                padding: 1vw;
                border-radius: 0.6vw;
                //font-weight: bold;
            }
            div.totalPrice{
                background-color: var(--color-primary);
