main.products{

    section.section1{
        padding-top: 4rem;
        text-align: center;
        p.subtitle{
            color: var(--color-gray);
            font-size: 20pt;
        }
        p.title{
            font-weight: 700;
            font-size: 40pt;
            color: var(--color-primary2);
        }
    }

    section.products{
        display: flex;
        flex-wrap: wrap; /* Permite que as divs se movam para a próxima linha */
        max-width: 100vw; /* Defina a largura máxima do container */
        justify-content: center; 
        div{
        }
    }

    section.section2{
        div.title{
            display: flex;
            justify-content: space-around;
            align-items: center;
            margin: 3rem 0;
            div.line{
                width: 500px;
                height: 1.4px;
                background-color: var(--color-primary);
            }
            p{
                font-weight: 700;
                color: var(--color-primary);
                font-size: 16pt;
            }
        }
    }

    section.section3{
        margin-top: 3rem;
        div.banner{
            position: relative;
            img.banner{
                width: 100vw;
            }
            div{
                position: absolute;
                top: 70px;
                left: 100px;
            }
            p{
                font-size: 20pt;
                color: white;
            }
            strong{
                font-size: 30pt;
                color: wheat;
            }
            input.inputText{
                width: 30rem;
                height: 3rem;
                padding-left: 2rem;
                border-radius: 30px;
                border: none;
                font-size: 14pt;
                outline: none;
                margin-top: 1rem;
            }
            input.inputSubmit{
                background-color: var(--color-primary);
                width: 6rem;
                height: 3rem;
                border-radius: 30px;
                border: none;
                outline: none;
                font-size: 14pt;
                margin-left: 1.4rem;
                color: white;
            }
        }
    }
    
}